<template>
    <v-row class="justify-center">
        <v-col cols="12" md="10">
            <v-alert
                v-model="alert"
                :type="alertType"
                border="left"
                close-text="Close Alert"
                dismissible
                transition="scale-transition"
            >
                {{ alertMessage }}
            </v-alert>

            <v-dialog
                v-model="loadingActions"
                hide-overlay
                persistent
                transition="dialog-bottom-transition"
                width="300"
            >
                <v-card :color="getAppColors.primary" dark>
                    <v-card-text>
                        Processing...
                        <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0"
                        ></v-progress-linear>
                    </v-card-text>
                </v-card>
            </v-dialog>

            <v-data-table
                :headers="headers"
                :items="disputes"
                :loading="loading"
                :search="search"
                class="elevation-4 rounded-lg"
                loading-text="Loading... Please wait"
                :footer-props="tableFooterProps"
                sort-by="caseDate"
                sort-desc
                v-show="!loadingActions"
            >
                <template v-slot:[`item.caseDate`]="{ item }">
                    {{ formatDate(item.caseDate) }}
                </template>
                <template v-slot:[`item.resolutionDate`]="{ item }">
                    {{ formatDate(item.resolutionDate) }}
                </template>
                <template v-slot:[`item.transactionDate`]="{ item }">
                    {{ formatDate(item.transactionDate) }}
                </template>
                <template v-slot:[`item.amount`]="{ item }">
                    {{ item.amount | currency }}
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                    <v-menu transition="scroll-y-transition" bottom left>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn icon v-bind="attrs" v-on="on">
                                <v-icon>mdi-dots-vertical</v-icon>
                            </v-btn>
                        </template>
                        <v-list>
                            <v-list-item>
                                <v-btn
                                    color="primary"
                                    text
                                    :href="
                                        `mailto:chargebacks@pps.io?subject=Case# ` +
                                        item.caseNumber +
                                        ` Letter Request`
                                    "
                                >
                                    Request Case Letter
                                </v-btn>
                            </v-list-item>
                            <v-list-item>
                                <v-btn
                                    color="primary"
                                    text
                                    href="https://app.midigator.com/login"
                                    target="_blank"
                                >
                                    Midigator
                                </v-btn>
                            </v-list-item>
                        </v-list>
                    </v-menu>
                </template>
                <template v-slot:top>
                    <v-toolbar
                        color="green darken-2"
                        class="rounded-t-lg"
                        dark
                        flat
                    >
                        <v-toolbar-title>Disputes</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-text-field
                            v-model="search"
                            append-icon="search"
                            label="Search"
                            single-line
                            hide-details
                            clearable
                            dark
                        />
                        <v-divider class="mx-4" inset vertical />
                        <v-select
                            v-model="quickDateRange"
                            :color="getAppColors.primary"
                            item-color="green"
                            :items="getQuickDateRangeChoices"
                            single-line
                            hide-details
                            return-object
                            @change="initialize"
                        />
                    </v-toolbar>
                </template>
                <template v-slot:no-data>
                    <v-alert prominent text type="info" class="ma-2">
                        <v-row align="center">
                            <v-col class="grow"> No records found </v-col>
                            <v-col class="shrink">
                                <v-btn
                                    color="green"
                                    class="my-2 white--text"
                                    @click="initialize"
                                >
                                    Retry
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-alert>
                </template>
            </v-data-table>
        </v-col>
    </v-row>
</template>

<script>
import accounting from 'accounting';
import axios from 'axios';
import moment from 'moment';
import { mapGetters } from 'vuex';

export default {
    name: 'DisputeList',
    data() {
        return {
            datePickerMenu: false,
            loading: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            search: '',
            tableFooterProps: {
                showFirstLastPage: true,
            },
            headers: [
                { text: 'Date Opened', align: 'left', value: 'caseDate' },
                {
                    text: 'Date Resolved',
                    align: 'left',
                    value: 'resolutionDate',
                },
                {
                    text: 'Transaction Date',
                    align: 'left',
                    value: 'transactionDate',
                },
                { text: 'Case Number', align: 'left', value: 'caseNumber' },
                {
                    text: 'Transaction ID',
                    align: 'left',
                    value: 'transactionId',
                },
                {
                    text: 'Case Type',
                    align: 'left',
                    value: 'caseTypeDescription',
                },
                {
                    text: 'Item Type',
                    align: 'left',
                    value: 'itemTypeDescription',
                },
                { text: 'Reason', align: 'left', value: 'disputeDesc' },
                { text: 'Amount', align: 'right', value: 'amount' },
                { text: 'Name', align: 'left', value: 'name' },
                {
                    text: 'Actions',
                    align: 'right',
                    value: 'actions',
                },
            ],
            disputes: [],
            quickDateRange: {
                text: 'Past 1 Year',
                value: 'last_1_y',
            },
            loadingActions: false,
        };
    },
    computed: {
        ...mapGetters([
            'getAppColors',
            'getInternalMxController',
            'getQuickDateRangeChoices',
        ]),
    },
    mounted() {
        this.initialize();
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        async initialize() {
            if (this.quickDateRange == null) return;

            this.loading = true;
            this.alert = false;

            const dateRange = this.quickDateRange
                ? this.quickDateRange.value
                : '';

            try {
                let response = await axios.get(
                    `${this.getInternalMxController + '/get_disputes'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            date_range: dateRange,
                        },
                    },
                );

                this.loading = false;

                if (response.data.warning) {
                    this.openAlert('info', response.data.message);
                } else {
                    if (response.data.data.totalRecords > 0) {
                        this.disputes = response.data.data.records;
                    } else {
                        console.log('No disputes found');
                        this.disputes = [];
                    }
                }
            } catch (error) {
                console.error('Could not get list of disputes');
                console.error(error);

                this.openAlert('error', error.toString());

                this.loading = false;
                this.disputes = [];
            }
        },
        formatDateTime(rawDateTime) {
            return rawDateTime
                ? moment(rawDateTime).format('MM/DD/YYYY LT')
                : '';
        },
        formatDate(rawDateTime) {
            return rawDateTime ? moment(rawDateTime).format('MM/DD/YYYY') : '';
        },
    },
    filters: {
        titleCase: (value) => {
            if (!value) return '';

            value = value.toString().toLowerCase().split(' ');

            for (let i = 0; i < value.length; i++) {
                value[i] = value[i][0].toUpperCase() + value[i].slice(1);
            }

            return value.join(' ');
        },
        currency: (value) => {
            if (!value) return '0';
            return accounting.formatMoney(value);
        },
    },
};
</script>
